


import { Observable } from 'rxjs';
import { TDSSafeAny } from 'tds-ui/shared/utility';

export type TDSTabPosition = 'top' | 'bottom' | 'left' | 'right';
export type TDSTabType = 'line' | 'card' | 'editable-card';
export type TDSTabsCanDeactivateFn = (fromIndex: number, toIndex: number) => Observable<boolean> | Promise<boolean> | boolean;
export type TDSTabPositionMode = 'horizontal' | 'vertical';

export interface TDSAnimatedInterface {
  inkBar: boolean;
  tabPane: boolean;
}

export class TDSTabChangeEvent {
  index?: number;
  tab: TDSSafeAny;
}

export interface TDSTabScrollListOffset {
  x: number;
  y: number;
}

export type TDSTabScrollListOffsetEvent = TDSTabScrollListOffset & { event: Event };

interface TDSTabWheelScrollEvent {
  type: 'wheel';
  event: WheelEvent;
}

interface TDSTabTouchScrollEvent {
  type: 'touchstart' | 'touchmove' | 'touchend';
  event: TouchEvent;
}

export type TDSTabScrollEvent = TDSTabTouchScrollEvent | TDSTabWheelScrollEvent;
export type TDSTabScrollEventHandlerFun<T extends TDSTabScrollEvent['event']> = (event: T) => void;

export interface TabTemplateContext {
  visible: boolean;
  active: boolean;
}
