

import { BooleanInput, coerceNumberProperty, NumberInput } from '@angular/cdk/coercion';
/** get some code from https://github.com/angular/material2 */

import { Direction, Directionality } from '@angular/cdk/bidi';
import {
  AfterContentChecked,
  AfterContentInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChildren,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  QueryList,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { NavigationEnd, Router, RouterLink, RouterLinkWithHref } from '@angular/router';

import { merge, Observable, of, Subject, Subscription } from 'rxjs';
import { delay, filter, first, startWith, takeUntil } from 'rxjs/operators';
import {
  TDSAnimatedInterface,
  TDSTabChangeEvent,
  TDSTabPosition,
  TDSTabPositionMode,
  TDSTabsCanDeactivateFn,
  TDSTabScrollEvent,
  TDSTabType
} from './interfaces';
import { TDSTabNavBarComponent } from './tab-nav-bar.component';
import { TDSTabComponent, TDS_TAB_SET } from './tab.component';
import { InputBoolean, TDSSafeAny } from 'tds-ui/shared/utility';
import { TDSConfigService, WithConfig } from 'tds-ui/core/config';
import { wrapIntoObservable } from 'tds-ui/core/util';

const TDS_CONFIG_MODULE_NAME=  'tabs';

let nextId = 0;

@Component({
  selector: 'tds-tabset',
  exportAs: 'tdsTabset',
  preserveWhitespaces: false,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.Default,
  providers: [
    {
      provide: TDS_TAB_SET,
      useExisting: TDSTabSetComponent
    }
  ],
  templateUrl:'./tabset.component.html',
  // template: `
  //   <tds-tabs-nav
  //     *ngIf="tabs.length"
  //     [ngStyle]="tabBarStyle"
  //     [_selectedIndex]="selectedIndex || 0"
  //     [inkBarAnimated]="inkBarAnimated"
  //     [addable]="addable"
  //     [addIcon]="addIcon"
  //     [hideBar]="hideAll"
  //     [position]="position"
  //     [tabPosition]="tabPosition"
  //     [extraTemplate]="tabBarExtraContent"
  //     (tabScroll)="tabListScroll.emit($event)"
  //     (selectFocusedIndex)="setSelectedIndex($event)"
  //     (addClicked)="_onAdd()"
  //     class="px-4"
      
  //   >
  //     <div
  //       class="tds-tabs-tab relative  items-center inline-flex outline-none cursor-pointer p-4 text-body-2"
  //       [style.margin-right.px]="position === 'horizontal' ? tabBarGutter : null"
  //       [style.margin-bottom.px]="position === 'vertical' ? tabBarGutter : null"
  //       [class.border-b-2]="position === 'horizontal'"
  //       [class.border-r-2]="tabPosition === 'left'"
  //       [class.border-l-2]="tabPosition === 'right'"        
  //       [class.tds-tabs-tab-active]="selectedIndex === i"
  //       [class.text-neutral-1-900]="selectedIndex === i"
  //       [class.text-neutral-1-600]="selectedIndex !== i"
  //       [class.border-primary-1]="selectedIndex === i"
  //       [class.border-transparent]="selectedIndex !== i"
  //       [class.font-semibold]="selectedIndex === i"
  //       [class.font-regular]="selectedIndex !== i"
  //       [class.tds-tabs-tab-disabled]="tab.disabled"
  //       (click)="clickNavItem(tab, i, $event)"
  //       (contextmenu)="contextmenuNavItem(tab, $event)"
  //       *ngFor="let tab of tabs; let i = index"
  //     >
  //       <div
  //         role="tab"
  //         [attr.tabIndex]="getTabIndex(tab, i)"
  //         [attr.aria-disabled]="tab.disabled"
  //         [attr.aria-selected]="selectedIndex === i && !hideAll"
  //         [attr.aria-controls]="getTabContentId(i)"
  //         [disabled]="tab.disabled"
  //         [tab]="tab"
  //         [active]="selectedIndex === i"
  //         class="tds-tabs-tab-btn focus:outline-none"
  //         tdsTabNavItem
  //         cdkMonitorElementFocus
  //       >
        
  //         <ng-container *tdsStringTemplateOutlet="tab.label; context: { visible: true , active: selectedIndex === i}">{{ tab.label }}</ng-container>
  //         <button
  //           tds-tab-close-button
  //           *ngIf="tab.closable && closable && !tab.disabled"
  //           [closeIcon]="tab.closeIcon"
  //           (click)="_onClose(i, $event)"
  //         ></button>
  //       </div>
  //     </div>
  //   </tds-tabs-nav>
  //   <div class="tds-tabs-content-holder p-4 w-full">
  //     <div
  //       class="tds-tabs-content w-full"
  //       [class.tds-tabs-content-top]="tabPosition === 'top'"
  //       [class.tds-tabs-content-bottom]="tabPosition === 'bottom'"
  //       [class.tds-tabs-content-left]="tabPosition === 'left'"
  //       [class.tds-tabs-content-right]="tabPosition === 'right'"
  //       [class.tds-tabs-content-animated]="tabPaneAnimated"
  //       [style.margin-left]="getTabContentMarginLeft()"
  //       [style.margin-right]="getTabContentMarginRight()"
  //     >
  //       <div
  //         tds-tab-body
  //         *ngFor="let tab of tabs; let i = index"
  //         [active]="selectedIndex == i && !hideAll"
  //         [content]="tab.content"
  //         [forceRender]="tab.forceRender"
  //         [tabPaneAnimated]="tabPaneAnimated"
  //       ></div>
  //     </div>
  //   </div>
  // `,
  host: {
    class: 'tds-tabs box-border m-0 p-0 flex overflow-hidden w-full ',
    '[class.tds-tabs-card]': `type === 'card' || type === 'editable-card'`,
    '[class.tds-tabs-editable]': `type === 'editable-card'`,
    '[class.tds-tabs-editable-card]': `type === 'editable-card'`,
    '[class.tds-tabs-centered]': `centered`,
    '[class.tds-tabs-rtl]': `dir === 'rtl'`,
    '[class.tds-tabs-top]': `tabPosition === 'top'`,
    '[class.flex-col]': `tabPosition === 'top' || tabPosition === 'bottom'`,
    '[class.tds-tabs-bottom]': `tabPosition === 'bottom'`,
    '[class.tds-tabs-left]': `tabPosition === 'left'`,
    '[class.tds-tabs-right]': `tabPosition === 'right'`,
    '[class.flex-row-reverse]': `tabPosition === 'right'`,
    '[class.flex-col-reverse]': `tabPosition === 'bottom'`,
    '[class.py-4]': `tabPosition === 'right'`,
    '[class.tds-tabs-default]': `size === 'default'`,
    '[class.tds-tabs-small]': `size === 'small'`,
    '[class.tds-tabs-large]': `size === 'large'`
  }
})
export class TDSTabSetComponent implements OnInit, AfterContentChecked, OnDestroy, AfterContentInit {

  static ngAcceptInputType_hideAdd: BooleanInput;
  static ngAcceptInputType_hideAll: BooleanInput;
  static ngAcceptInputType_centered: BooleanInput;
  static ngAcceptInputType_linkRouter: BooleanInput;
  static ngAcceptInputType_linkExact: BooleanInput;
  static ngAcceptInputType_selectedIndex: NumberInput;

  @Input()
  get selectedIndex(): number | null {
    return this._selectedIndex;
  }
  set selectedIndex(value: null | number) {
    this.indexToSelect = coerceNumberProperty(value, null);
  }
  @Input() tabPosition: TDSTabPosition = 'top';
  @Input() tabBarExtraContent?: TemplateRef<void>;
  @Input() onCanDeactivate: TDSTabsCanDeactivateFn | null = null;
  @Input() addIcon: string | TemplateRef<TDSSafeAny> = 'plus';
  @Input() tabBarStyle: { [key: string]: string } | null = null;
  @Input() @WithConfig() type: TDSTabType = 'line';
  @Input() @WithConfig() size = 'default';
  @Input() @WithConfig() animated: TDSAnimatedInterface | boolean = false;
  @Input() @WithConfig() tabBarGutter?: number = undefined;
  @Input() @InputBoolean() hideAdd: boolean = false;
  @Input() @InputBoolean() centered: boolean = false;
  @Input() @InputBoolean() hideAll = false;
  @Input() @InputBoolean() linkRouter = false;
  @Input() @InputBoolean() linkExact = true;
  @Input() @InputBoolean() hidenNextBtn = false;
  @Input() clsTab :string = 'px-4';
  @Output() readonly selectChange: EventEmitter<TDSTabChangeEvent> = new EventEmitter<TDSTabChangeEvent>(true);
  @Output() readonly selectedIndexChange: EventEmitter<number> = new EventEmitter<number>();
  @Output() readonly tabListScroll = new EventEmitter<TDSTabScrollEvent>();
  @Output() readonly close = new EventEmitter<{ index: number }>();
  @Output() readonly add = new EventEmitter<void>();

  get position(): TDSTabPositionMode {
    return ['top', 'bottom'].indexOf(this.tabPosition) === -1 ? 'vertical' : 'horizontal';
  }

  get addable(): boolean {
    return this.type === 'editable-card' && !this.hideAdd;
  }

  get closable(): boolean {
    return this.type === 'editable-card';
  }

  get line(): boolean {
    return this.type === 'line';
  }

  get inkBarAnimated(): boolean {
    return this.line && (typeof this.animated === 'boolean' ? this.animated : this.animated.inkBar);
  }

  get tabPaneAnimated(): boolean {
    return  false
    // (
    //   this.position === 'horizontal' && this.line && (typeof this.animated === 'boolean' ? this.animated : this.animated.tabPane)
    // );
  }

  // Pick up only direct descendants under ivy rendering engine
  // We filter out only the tabs that belong to this tab set in `tabs`.
  @ContentChildren(TDSTabComponent, { descendants: true }) allTabs: QueryList<TDSTabComponent> = new QueryList<TDSTabComponent>();
  @ViewChild(TDSTabNavBarComponent, { static: false }) tabNavBarRef!: TDSTabNavBarComponent;

  // All the direct tabs for this tab set
  tabs: QueryList<TDSTabComponent> = new QueryList<TDSTabComponent>();

  dir: Direction = 'ltr';
  private readonly tabSetId!: number;
  private destroy$ = new Subject<void>();
  private indexToSelect: number | null = 0;
  private _selectedIndex: number | null = null;
  private tabLabelSubscription = Subscription.EMPTY;
  private tabsSubscription = Subscription.EMPTY;
  private canDeactivateSubscription = Subscription.EMPTY;

  constructor(
    public tdsConfigService: TDSConfigService,
    private cdr: ChangeDetectorRef,
    @Optional() private directionality: Directionality,
    @Optional() private router: Router
  ) {
    this.tabSetId = nextId++;
  }

  ngOnInit(): void {
    this.dir = this.directionality.value;
    this.directionality.change?.pipe(takeUntil(this.destroy$)).subscribe((direction: Direction) => {
      this.dir = direction;
      this.cdr.detectChanges();
    });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
    this.tabs.destroy();
    this.tabLabelSubscription.unsubscribe();
    this.tabsSubscription.unsubscribe();
    this.canDeactivateSubscription.unsubscribe();
  }

  ngAfterContentInit(): void {
    Promise.resolve().then(() => {
      this.setUpRouter();
    });
    this.subscribeToTabLabels();
    this.subscribeToAllTabChanges();

    // Subscribe to changes in the amount of tabs, in order to be
    // able to re-render the content as new tabs are added or removed.
    this.tabsSubscription = this.tabs.changes.subscribe(() => {
      const indexToSelect = this.clampTabIndex(this.indexToSelect);

      // Maintain the previously-selected tab if a new tab is added or removed and there is no
      // explicit change that selects a different tab.
      if (indexToSelect === this._selectedIndex) {
        const tabs = this.tabs.toArray();

        for (let i = 0; i < tabs.length; i++) {
          if (tabs[i].isActive) {
            // Assign both to the `indexToSelect` and `_selectedIndex` so we don't fire a changed
            // event, otherwise the consumer may end up in an infinite loop in some edge cases like
            // adding a tab within the `selectedIndexChange` event.
            this.indexToSelect = this._selectedIndex = i;
            break;
          }
        }
      }
      this.subscribeToTabLabels();
      this.cdr.markForCheck();
    });
  }

  ngAfterContentChecked(): void {
    // Don't clamp the `indexToSelect` immediately in the setter because it can happen that
    // the amount of tabs changes before the actual change detection runs.
    const indexToSelect = (this.indexToSelect = this.clampTabIndex(this.indexToSelect));

    // If there is a change in selected index, emit a change event. Should not trigger if
    // the selected index has not yet been initialized.
    if (this._selectedIndex !== indexToSelect) {
      const isFirstRun = this._selectedIndex == null;

      if (!isFirstRun) {
        this.selectChange.emit(this.createChangeEvent(indexToSelect));
      }

      // Changing these values after change detection has run
      // since the checked content may contain references to them.
      Promise.resolve().then(() => {
        this.tabs.forEach((tab, index) => (tab.isActive = index === indexToSelect));

        if (!isFirstRun) {
          this.selectedIndexChange.emit(indexToSelect);
        }
      });
    }

    // Setup the position for each tab and optionally setup an origin on the next selected tab.
    this.tabs.forEach((tab: TDSTabComponent, index: number) => {
      tab.position = index - indexToSelect;

      // If there is already a selected tab, then set up an origin for the next selected tab
      // if it doesn't have one already.
      if (this._selectedIndex != null && tab.position === 0 && !tab.origin) {
        tab.origin = indexToSelect - this._selectedIndex;
      }
    });

    if (this._selectedIndex !== indexToSelect) {
      this._selectedIndex = indexToSelect;
      this.cdr.markForCheck();
    }
  }

  _onClose(index: number, e: MouseEvent): void {
    e.preventDefault();
    e.stopPropagation();
    this.close.emit({ index });
  }

  _onAdd(): void {
    this.add.emit();
  }

  private clampTabIndex(index: number | null): number {
    return Math.min(this.tabs.length - 1, Math.max(index || 0, 0));
  }

  private createChangeEvent(index: number): TDSTabChangeEvent {
    const event = new TDSTabChangeEvent();
    event.index = index;
    if (this.tabs && this.tabs.length) {
      event.tab = this.tabs.toArray()[index];
      this.tabs.forEach((tab, i) => {
        if (i !== index) {
          tab.tabDeselect.emit();
        }
      });
      event.tab.tabSelect.emit();
    }
    return event;
  }

  private subscribeToTabLabels(): void {
    if (this.tabLabelSubscription) {
      this.tabLabelSubscription.unsubscribe();
    }

    this.tabLabelSubscription = merge(...this.tabs.map(tab => tab.stateChanges)).subscribe(() => this.cdr.markForCheck());
  }

  private subscribeToAllTabChanges(): void {
    this.allTabs.changes.pipe(startWith(this.allTabs)).subscribe((tabs: QueryList<TDSTabComponent>) => {
      this.tabs.reset(tabs.filter(tab => tab.closestTabSet === this));
      this.tabs.notifyOnChanges();
    });
  }

  canDeactivateFun(pre: number, next: number): Observable<boolean> {
    if (typeof this.onCanDeactivate === 'function') {
      const observable = wrapIntoObservable(this.onCanDeactivate(pre, next));
      return observable.pipe(first(), takeUntil(this.destroy$));
    } else {
      return of(true);
    }
  }

  clickNavItem(tab: TDSTabComponent, index: number, e: MouseEvent): void {
    if (!tab.disabled) {
      // ignore onCanDeactivate
      tab.tabClick.emit();
      if (!this.isRouterLinkClickEvent(index, e)) {
        this.setSelectedIndex(index);
      }
    }
  }

  private isRouterLinkClickEvent(index: number, event: MouseEvent): boolean {
    const target = event.target as HTMLElement;
    if (this.linkRouter) {
      return !!this.tabs.toArray()[index]?.linkDirective?.elementRef.nativeElement.contains(target);
    } else {
      return false;
    }
  }

  contextmenuNavItem(tab: TDSTabComponent, e: MouseEvent): void {
    if (!tab.disabled) {
      // ignore onCanDeactivate
      tab.tabContextmenu.emit(e);
    }
  }

  setSelectedIndex(index: number): void {
    this.canDeactivateSubscription.unsubscribe();
    this.canDeactivateSubscription = this.canDeactivateFun(this._selectedIndex!, index).subscribe(can => {
      if (can) {
        this.selectedIndex = index;
        this.tabNavBarRef.focusIndex = index;
        this.cdr.markForCheck();
      }
    });
  }

  getTabIndex(tab: TDSTabComponent, index: number): number | null {
    if (tab.disabled) {
      return null;
    }
    return this._selectedIndex === index ? 0 : -1;
  }

  getTabContentId(i: number): string {
    return `tds-tabs-${this.tabSetId}-tab-${i}`;
  }

  private setUpRouter(): void {
    if (this.linkRouter) {
      if (!this.router) {
        throw new Error(`[TDS] you should import 'RouterModule' if you want to use 'linkRouter'!`);
      }
      this.router.events
        .pipe(
          takeUntil(this.destroy$),
          filter(e => e instanceof NavigationEnd),
          startWith(true),
          delay(0)
        )
        .subscribe(() => {
          this.updateRouterActive();
          this.cdr.markForCheck();
        });
    }
  }

  private updateRouterActive(): void {
    if (this.router.navigated) {
      const index = this.findShouldActiveTabIndex();
      if (index !== this._selectedIndex) {
        this.setSelectedIndex(index);
      }
      this.hideAll = index === -1;
    }
  }

  private findShouldActiveTabIndex(): number {
    const tabs = this.tabs.toArray();
    const isActive = this.isLinkActive(this.router);

    return tabs.findIndex(tab => {
      const c = tab.linkDirective;
      return c ? isActive(c.routerLink) || isActive(c.routerLinkWithHref) : false;
    });
  }

  private isLinkActive(router: Router): (link?: RouterLink | RouterLinkWithHref) => boolean {
    return (link?: RouterLink | RouterLinkWithHref) => (link ? router.isActive(link.urlTree!, this.linkExact) : false);
  }

  private getTabContentMarginValue(): number {
    return -(this.selectedIndex || 0) * 100;
  }

  getTabContentMarginLeft(): string {
    if (this.tabPaneAnimated) {
      if (this.dir !== 'rtl') {
        return this.getTabContentMarginValue() + '%';
      }
    }
    return '';
  }
  getTabContentMarginRight(): string {
    if (this.tabPaneAnimated) {
      if (this.dir === 'rtl') {
        return this.getTabContentMarginValue() + '%';
      }
    }
    return '';
  }
}
