

import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnDestroy,
  Output,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';

import { TDSSafeAny } from 'tds-ui/shared/utility';

import { TDSTabNavItemDirective } from './tab-nav-item.directive';

@Component({
  selector: 'tds-tab-nav-operation',
  exportAs: 'tdsTabNavOperation',
  preserveWhitespaces: false,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  templateUrl:'./tab-nav-operation.component.html',
  // template: `<div></div>`,
  // template: `<div></div>
  //   <button
  //     tds-dropdown
  //     class="tds-tabs-nav-more"
  //     type="button"
  //     tabindex="-1"
  //     aria-hidden="true"
  //     overlayClassName="tds-tabs-dropdown"
  //     #dropdownTrigger="tdsDropdown"
  //     [tdsDropdownMenu]="menu"
  //     [overlayStyle]="{ minWidth: '46px' }"
  //     [nzMatchWidthElement]="null"
  //     (visibleChange)="menuVisChange($event)"
  //     (mouseenter)="showItems()"
  //   >
  //     <i class='tdsi-three-dots-horizon-fill'></i>
  //   </button>
  //   <tds-dropdown-menu #menu="tdsDropdownMenu">
  //     <ul nz-menu *ngIf="menuOpened">
  //       <li
  //         nz-menu-item
  //         *ngFor="let item of items"
  //         class="tds-tabs-dropdown-menu-item"
  //         [class.tds-tabs-dropdown-menu-item-disabled]="item.disabled"
  //         [nzSelected]="item.active"
  //         [disabled]="item.disabled"
  //         (click)="onSelect(item)"
  //         (contextmenu)="onContextmenu(item, $event)"
  //       >
  //         <ng-container *tdsStringTemplateOutlet="item.tab.label; context: { visible: false }">{{ item.tab.label }}</ng-container>
  //       </li>
  //     </ul>
  //   </tds-dropdown-menu>
  //   <button *ngIf="addable" tds-tab-add-button [addIcon]="addIcon" (click)="addClicked.emit()"></button>
  // `,
  host: {
    class: 'tds-tabs-nav-operations flex items-stretch',
    '[class.tds-tabs-nav-operations-hidden]': 'items.length === 0',
    '[class.hidden]': 'items.length === 0'
  }
})
export class TDSTabNavOperationComponent implements OnDestroy {
  @Input() items: TDSTabNavItemDirective[] = [];
  @Input() addable: boolean = false;
  @Input() addIcon: string | TemplateRef<TDSSafeAny> = 'plus';
  @Input() hidenNextBtn : boolean =false;
  @Output() readonly nextClicked = new EventEmitter<void>();
  @Output() readonly addClicked = new EventEmitter<void>();
  @Output() readonly selected = new EventEmitter<TDSTabNavItemDirective>();

  closeAnimationWaitTimeoutId:TDSSafeAny = -1;
  menuOpened = false;

  private readonly element: HTMLElement;
  constructor(public cdr: ChangeDetectorRef, private elementRef: ElementRef<HTMLElement>) {
    this.element = this.elementRef.nativeElement;
  }

  onSelect(item: TDSTabNavItemDirective): void {
    if (!item.disabled) {
      // ignore onCanDeactivate
      item.tab.tabClick.emit();
      this.selected.emit(item);
    }
  }

  onContextmenu(item: TDSTabNavItemDirective, e: MouseEvent): void {
    if (!item.disabled) {
      item.tab.tabContextmenu.emit(e);
    }
  }
  showItems(): void {
    clearTimeout(this.closeAnimationWaitTimeoutId);
    this.menuOpened = true;
    this.cdr.markForCheck();
  }

  menuVisChange(visible: boolean): void {
    if (!visible) {
      this.closeAnimationWaitTimeoutId = setTimeout(() => {
        this.menuOpened = false;
        this.cdr.markForCheck();
      }, 150);
    }
  }

  getElementWidth(): number {
    return this.element?.offsetWidth || 0;
  }

  getElementHeight(): number {
    return this.element?.offsetHeight || 0;
  }

  ngOnDestroy(): void {
    clearTimeout(this.closeAnimationWaitTimeoutId);
  }
  onclickNext(e:MouseEvent)
  {
    this.nextClicked.emit();
  }
}
