

import { Component, ElementRef, Input, TemplateRef } from '@angular/core';
import { TDSSafeAny } from 'tds-ui/shared/utility';



@Component({
  selector: 'tds-tab-add-button, button[tds-tab-add-button]',
  template: `
    <ng-container *tdsStringTemplateOutlet="addIcon; let icon">
     
    </ng-container>
  `,
  host: {
    class: 'tds-tabs-nav-add',
    'aria-label': 'Add tab',
    type: 'button'
  }
})
export class TDSTabAddButtonComponent {
  @Input() addIcon: string | TemplateRef<TDSSafeAny> = 'plus';

  private readonly element: HTMLElement;

  constructor(private elementRef: ElementRef<HTMLElement>) {
    this.element = this.elementRef.nativeElement;
  }

  getElementWidth(): number {
    return this.element?.offsetWidth || 0;
  }

  getElementHeight(): number {
    return this.element?.offsetHeight || 0;
  }
}
