

import {
  ChangeDetectionStrategy,
  Component,
  ContentChild,
  EventEmitter,
  Inject,
  InjectionToken,
  Input,
  OnChanges,
  OnDestroy,
  Output,
  SimpleChanges,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { TabTemplateContext } from './interfaces';

import { Subject } from 'rxjs';

import { TDSTabLinkDirective, TDSTabLinkTemplateDirective } from './tab-link.directive';
import { TDSTabDirective } from './tab.directive';
import { BooleanInput } from '@angular/cdk/coercion';
import { InputBoolean, TDSSafeAny } from 'tds-ui/shared/utility';

/**
 * Used to provide a tab set to a tab without causing a circular dependency.
 */
export const TDS_TAB_SET = new InjectionToken<TDSSafeAny>('TDS_TAB_SET');

@Component({
  selector: 'tds-tab',
  exportAs: 'tdsTab',
  preserveWhitespaces: false,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  template: `
    <ng-template #tabLinkTemplate>
      <ng-content select="[tds-tab-link]"></ng-content>
    </ng-template>
    <ng-template #contentTemplate>
      <ng-content></ng-content>
    </ng-template>
  `
})
export class TDSTabComponent implements OnChanges, OnDestroy {
  static ngAcceptInputType_disabled: BooleanInput;
  static ngAcceptInputType_closable: BooleanInput;
  static ngAcceptInputType_forceRender: BooleanInput;

  @Input() title: string | TemplateRef<TabTemplateContext> = '';
  @Input() @InputBoolean() closable = false;
  @Input() closeIcon: string | TemplateRef<TDSSafeAny> = 'close';
  @Input() @InputBoolean() disabled = false;
  @Input() @InputBoolean() forceRender = false;
  @Input() clsContent:string="p-4 w-full h-full";
  @Output() readonly tabSelect = new EventEmitter<void>();
  @Output() readonly tabDeselect = new EventEmitter<void>();
  @Output() readonly tabClick = new EventEmitter<void>();
  @Output() readonly tabContextmenu = new EventEmitter<MouseEvent>();

  @ContentChild(TDSTabLinkTemplateDirective, { static: false }) tabLinkTemplateDirective!: TDSTabLinkTemplateDirective;
  @ContentChild(TDSTabDirective, { static: false, read: TemplateRef }) template: TemplateRef<void> | null = null;
  @ContentChild(TDSTabLinkDirective, { static: false }) linkDirective!: TDSTabLinkDirective;
  @ViewChild('contentTemplate', { static: true }) contentTemplate!: TemplateRef<TDSSafeAny>;

  isActive: boolean = false;
  position: number | null = null;
  origin: number | null = null;
  readonly stateChanges = new Subject<void>();

  get content(): TemplateRef<TDSSafeAny> {
    return this.template || this.contentTemplate;
  }

  get label(): string | TemplateRef<TDSSafeAny> {
    return this.title || this.tabLinkTemplateDirective?.templateRef;
  }

  constructor(@Inject(TDS_TAB_SET) public closestTabSet: TDSSafeAny) {}

  ngOnChanges(changes: SimpleChanges): void {
    
    const { title, disabled, forceRender } = changes;
    if (title || disabled || forceRender) {
      this.stateChanges.next();
    }
  }

  ngOnDestroy(): void {
    this.stateChanges.complete();
  }
}
